<template>
	<view class="content">
		<!-- 自定义组件 -->
		<navbar></navbar>
		<tab :list="tabList"></tab>
		
	</view>
</template>

<script>
	import navbar from '@/components/navbar/navbar.vue'
	import tab from '@/components/tab/tab.vue'
	export default {
		components:{
			navbar
		},
		data() {
			return {
				title: 'Hello',
				tabList:[]
			}
		},
		onLoad() {
			this.getLabel()
		},
		methods: {
			getLabel(){
				let self=this
				uniCloud.callFunction({
					name:'get_label',
					success(res) {
						console.log(res);
						if(res.result.code==200){
							self.tabList=res.result.data
						}
					},
					fail(err) {
						console.log(err);
					}
				})
			}
		}
	}
</script>

